            let that = this;
            let url = 'http://'+that.url+'/room/remove_roomnumber/'+row.id+'/';
            that.$axios({
              method : 'post',
              url : url
            }).then(function (res) {
              console.info(res);
              TableData.splice(index, 1);
              that.$message({
                message: that.hint,
                type: 'success'
              });
            }).catch(function (err) {
              console.info(err);
            })
            console.info(index,row.id);
