                  building_name : that.param.building_name,
                  is_halt : that.param.is_halt,
                  parent_code : that.param.parent_code,
                }
              }).then(function (res) {
                that.dialogVisibleLd = false;
                console.info(res);
              }).catch(function (err) {
                console.info(err);
              })
            }else{
              that.$message({
                message: '*',
                type: 'warning'
              });
            }
          },
          /**
           * @saveInfoLc 
           * @param no
